<template>
  <el-form
    :model="accessPoint"
    :rules="rules"
    ref="dataForm"
    label-width="100px"
    style="margin-top: 15px"
  >
    <el-row>
      <el-col :span="16">
        <el-form-item label="API名称" prop="name">
          <el-input v-model="accessPoint.name" />
        </el-form-item>
      </el-col>
    </el-row>
    <el-row>
      <el-col :span="8">
        <el-form-item label="类型" prop="accessPointTypeId">
          <el-select v-model="accessPoint.accessPointTypeId" placeholder="类型">
            <el-option
              v-for="item in codeOptions"
              :key="item.id"
              :label="item.name"
              :value="item.id"
            />
          </el-select>
        </el-form-item>
      </el-col>
      <el-col :span="8">
        <el-form-item label="单价(元)" prop="price">
          <el-input v-model="accessPoint.price" />
        </el-form-item>
      </el-col>
    </el-row>
    <el-form-item
      v-for="(param, index) in accessPoint.params"
      :key="param.id"
      :label="'参数' + (index + 1)"
    >
      <el-row>
        <el-col :span="8">
          <el-form-item
            :prop="'params.' + index + '.name'"
            label="参数名"
            :rules="{
              required: true,
              message: '名称不能为空',
              trigger: 'blur'
            }"
          >
            <el-input v-model="param.name" />
          </el-form-item>
        </el-col>
        <el-col :span="8">
          <el-form-item
            :prop="'params.' + index + '.location'"
            label="位置"
            :rules="{
              required: true,
              message: '请选择位置',
              trigger: 'change'
            }"
          >
            <el-select v-model="param.location" placeholder="位置">
              <el-option
                v-for="item in locationOptions"
                :key="item.value"
                :label="item.lable"
                :value="item.value"
              />
            </el-select>
          </el-form-item>
        </el-col>
      </el-row>
      <el-row style="margin-top: 10px;">
        <el-col :span="4">
          <el-form-item label="必填" :prop="'params.' + index + '.required'">
            <el-switch v-model="param.required" />
          </el-form-item>
        </el-col>
        <el-col :span="4">
          <el-form-item label="标识" :prop="'params.' + index + '.marked'">
            <el-switch v-model="param.marked" />
          </el-form-item>
        </el-col>
        <el-col :span="4">
          <el-form-item label="验证" :prop="'params.' + index + '.validated'">
            <el-switch v-model="param.validated" />
          </el-form-item>
        </el-col>
        <el-col :span="4">
          <el-form-item label="签名" :prop="'params.' + index + '.identified'">
            <el-switch v-model="param.identified" />
          </el-form-item>
        </el-col>
        <el-col :span="2" style="margin-left: 10px">
          <el-button @click.prevent="removeParam(param)">删除</el-button>
        </el-col>
      </el-row>
    </el-form-item>
    <el-form-item>
      <el-button type="primary" @click="submitForm('dataForm')">保存</el-button>
      <el-button @click="addParam">新增参数</el-button>
      <el-button @click="resetForm('dataForm')">重置</el-button>
    </el-form-item>
  </el-form>
</template>
<script>
import { get, create, update, accessType } from "@/api/accessPoint";
import { isMoney } from "@/utils/validate";

const defaultForm = {
  id: undefined,
  name: "",
  accessPointTypeId: undefined,
  price: 0.0,
  status: 1,
  params: [
    {
      id: "",
      name: "",
      location: "",
      required: false,
      marked: false,
      identified: false,
      validated: false,
      status: 1
    }
  ]
};

const locationOptions = [
  { value: "PARAMETER", lable: "PARAMETER" },
  { value: "JSON", lable: "JSON" }
];

export default {
  props: {
    isEdit: {
      type: Boolean,
      default: false
    }
  },
  created() {
    if (this.isEdit) {
      this.accessPoint.id = this.$route.params && this.$route.params.id;
      this.fetchData(this.accessPoint.id);
    } else {
      this.accessPoint = Object.assign({}, defaultForm);
    }
    accessType({ status: 1 }).then(response => {
      if (response.code === 0) {
        this.codeOptions = response.data.record;
      } else {
        this.$message({
          message: response.message,
          type: "warning"
        });
      }
    });
  },
  data() {
    const checkMoney = (rule, value, callback) => {
      if (value || value === 0) {
        if (!isMoney(value)) {
          callback(new Error("金额错误"));
        } else {
          callback();
        }
      } else {
        callback(new Error("金额不能为空"));
      }
    };
    return {
      locationOptions: locationOptions,
      idx: 1,
      codeOptions: [],
      accessPoint: Object.assign({}, defaultForm),
      rules: {
        name: [
          { required: true, message: "请输入API名称", trigger: "blur", max: 20 }
        ],
        accessPointTypeId: [
          { required: true, message: "请选择API类型", trigger: "change" }
        ],
        price: [{ required: true, validator: checkMoney, trigger: "blur" }]
      }
    };
  },
  methods: {
    fetchData(id) {
      get(id).then(response => {
        if (response.code === 0) {
          let data = response.data;
          this.accessPoint.name = data.name;
          this.accessPoint.accessPointTypeId = data.accessPointTypeId;
          this.accessPoint.price = data.price / 100;
          this.accessPoint.params = data.accessPointParamList;
          this.idx = data.accessPointParamList
            ? data.accessPointParamList.length + 1
            : 0;
        }
      });
    },
    submitForm() {
      this.$refs["dataForm"].validate(valid => {
        if (valid) {
          let ap = JSON.parse(JSON.stringify(this.accessPoint));
          ap.price = ap.price * 100;
          if (this.isEdit) {
            this.updateAccessPoint(ap);
          } else {
            this.createAccessPoint(ap);
          }
        }
      });
    },
    createAccessPoint(data) {
      create(data).then(response => {
        if (response.code === 0) {
          this.$notify({
            title: "成功",
            message: "保存成功",
            type: "success",
            duration: 2000
          });
          this.$router.push({ path: "/api/list" });
        }
      });
    },
    updateAccessPoint(data) {
      update(data).then(response => {
        if (response.code === 0) {
          this.$notify({
            title: "成功",
            message: "保存成功",
            type: "success",
            duration: 2000
          });
          this.$router.push({ path: "/api/list" });
        }
      });
    },
    resetForm(formName) {
      this.accessPoint.params = [];
      this.$refs[formName].resetFields();
    },
    removeParam(item) {
      var index = this.accessPoint.params.indexOf(item);
      if (index !== -1) {
        this.accessPoint.params.splice(index, 1);
      }
    },
    addParam() {
      this.accessPoint.params.push({
        id: this.idx++,
        name: "",
        location: "PARAMETER",
        required: false,
        marked: false,
        identified: false,
        validated: false,
        status: 1
      });
    }
  }
};
</script>
