import request from "@/utils/request";

export function list(params) {
  return request({
    url: "/accessPoint/list",
    method: "post",
    headers: {
      "Content-Type": "application/json"
    },
    data: params
  });
}

export function get(id) {
  return request({
    url: "/accessPoint/" + id,
    method: "get"
  });
}

